import styled from "styled-components";
import { Link } from "gatsby";
import React from "react";
import { AnimatedUnderlineText } from "components/Text";

const Feed = styled.section`
  margin-bottom: 40px;
`;

const FeedTitle = styled(Link)`
  font-size: 30px;
  font-weight: 450;
  line-height: 28px;
`;

const FeedMeta = styled.div`
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
  margin: 15px auto;
`;

const FeedTime = styled.time``;

const FeedTags = ({ tags }) => (
  <>
    {tags.map((tag, index) => {
      const comma = index === 0 ? "" : ",";
      return (
        <span key={index}>
          {comma}
          <Link to={`/tags/${tag}`}> {tag}</Link>
        </span>
      );
    })}
  </>
);

const FeedDescription = styled.p`
  font-size: 16px;
  margin: 0;
  margin-top: 5px;
  margin-bottom: 19.5px;
`;

const BlogFeedCard: React.FC<{ frontmatter: any; fields: any }> = ({
  frontmatter,
  fields,
}) => {
  const { title, description, date, tags } = frontmatter;
  return (
    <Feed>
      <FeedTitle to={fields.slug}>
        <AnimatedUnderlineText> {title}</AnimatedUnderlineText>
      </FeedTitle>
      <FeedMeta>
        <FeedTime>{date}</FeedTime> <br />
        tags: {tags && <FeedTags tags={tags} />}
      </FeedMeta>
      <FeedDescription>{description}</FeedDescription>
    </Feed>
  );
};

export default BlogFeedCard;
